* {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
}

body {
  background-image: url("./Assets/clouds.jpg");
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
  width: 100vw;
}

.All {
  max-width: 100vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:space-evenly;
  flex-wrap: wrap;
  font-size: 1rem;
}

.weatheroverview {
  width: 20rem;
  margin: 0 ;
  display: flex;
  flex-direction: column;
  line-height: 2em;
}

.weatherOnly {
  border-radius: 10px;
  margin: 0 auto;
  background-color: rgba(233, 232, 232, 0.1);
  padding: 8% 0;
  min-width: 85%;
  margin-top: 2em;
}

.containerCard {
  width: 100%;
  margin: 0 auto;
  overflow: auto;
  border: rgb(38, 57, 77);
  padding: 50px 30px 0 30px;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: transparent;
  height: 65vh;
  font-size: 100%;
} 

