.buttonContainer {
  margin: 0;
  width: auto;
  height: 100vh;
  padding: 2%;
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.citybutton {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  color: black;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  border: rgb(38, 57, 77);
  font-size: 1.2rem;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
  outline: none;
}

@media screen and (max-width: 768px) {
   .All {
     flex-direction: column;
     justify-content: center;
     align-items: center;
   }
   .buttonContainer {
     flex-direction: row;
     height: auto;
     margin-top: 1em;
   }
   .citybutton {
     width: 7.5rem;
     height: 7.5rem;
     font-size: 1rem;
   }
   .weatheroverview {
     margin-top: 1em;
   }
}

@media screen and (max-width: 360px) {
  .weatheroverview {
   width: 17rem;
  }
  .citybutton {
    width: 7rem;
    height: 7rem;
    font-size: 1rem;
  }
}

